import { useState } from 'react';

const Search = ({ onSearch = () => {} }) => {
    const [name, setName] = useState("");

    const onSubmitHandler = (e) => {
        e.preventDefault();
        onSearch(name);
    }

    return (
        <form onSubmit={onSubmitHandler}>
            <input className="border-2 border-blue-400 m-4 px-3 py-1 rounded-full" type="text" value={name} onChange={(e) => { setName(e.target.value) }} />
            <button className="border-blue-400 border-2 m-4 px-3 py-1 rounded-full hover:bg-blue-200" type="submit">Buscar</button>
        </form>
    );
}

export default Search;